export default {
  data() {
    return {
			dataInfo:[],
			swiperList:[],
			categoryList:[],
			bestGoodsList:[],
			likeGoodsList:[],
      // swiperList: [{
      //   id: 0,
      //   type: 'image',
      //   url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big84000.jpg'
      // }, {
      //   id: 1,
      //   type: 'image',
      //   url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big84001.jpg'
      // }, {
      //   id: 2,
      //   type: 'image',
      //   url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big39000.jpg'
      // }, {
      //   id: 3,
      //   type: 'image',
      //   url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg'
      // }],
      goodsList: [{
        image: 'https://www.vvwed.com/static/images/market/1111.png',
        title: '卡伯纳干红葡萄酒'
      }, {
        image: 'https://www.vvwed.com/static/images/market/2222.png',
        title: '图利斯葡萄酒'
      }, {
        image: 'https://www.vvwed.com/static/images/market/3333.png',
        title: '卡伯纳干红葡萄酒'
      }, {
        image: 'https://www.vvwed.com/static/images/market/4444.png',
        title: '沃隆坚果'
      }]
    };
  },

  components: {},
  props: {},

  /**
   * 生命周期函数--监听页面加载
   */
  onLoad: function (options) {
		var that = this;
		that.loadData();
		
	},

  /**
   * 生命周期函数--监听页面初次渲染完成
   */
  onReady: function () {},

  /**
   * 生命周期函数--监听页面显示
   */
  onShow: function () {},

  /**
   * 生命周期函数--监听页面隐藏
   */
  onHide: function () {},

  /**
   * 生命周期函数--监听页面卸载
   */
  onUnload: function () {},

  /**
   * 页面相关事件处理函数--监听用户下拉动作
   */
  onPullDownRefresh: function () {},

  /**
   * 页面上拉触底事件的处理函数
   */
  onReachBottom: function () {},

  /**
   * 用户点击右上角分享
   */
  onShareAppMessage: function () {},
  methods: {
		loadData:function(){
			var that = this;
			that.$api.getMarketData({}).then( res => {
				that.dataInfo = res.data;
				that.swiperList = that.dataInfo.llAdvMain.llAdvDetailList;
				that.categoryList = that.dataInfo.categoryList;
				that.bestGoodsList = that.dataInfo.bestGoodsList;
				that.likeGoodsList = that.dataInfo.likeGoodsList;
			}).catch()
		},
		
		
    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
    }
  }
};
